import React from 'react';

const appStyle = {
	textAlign: "center",
	fontFamily: 'Hind, sans-serif',
	position: 'fixed',
	height: '1100px',
	width: '100%'
}

const colorBlue = {
	//color: '#4fd8ff',
	backgroundColor: '#4fd8ff',
	height: '800px',
}

const headerText = {
	margin: '0px',
}
		
const Header = props => (
	<div style={appStyle}>
		<h1 style={headerText}>Front-end React Developer</h1>
		<p style={headerText}>I build websites using React.js</p>
	</div>
);

export default Header;